.cards {
  display: flex;
  max-width: 1024px;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
  padding: 0 40px;
}

.product-card {
  margin-top: 50px;
  width: 200px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 16px;
  
  &__image {
    display: block;
    margin: 0 auto 40px;
    width: 160px;
    height: 134px;
  }

  &__title {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: $text-accent-color;
    text-decoration: none;
  }

  &__code {
    margin-top: 4px;
    margin-bottom: 16px;
    font-weight: 400;
  }

  &__rating-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
  }

  &__rating-count {
    line-height: 14px;
    color: $text-accent-color;
  }

  &__price-container {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin-bottom: 16px;
    font-size: 12px;
    line-height: 18px;
  }

  &__price-current {
    font-weight: bold;
    font-size: 16px;
    color: $text-accent-color;
  }

  &__buy-button {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    border: 1px solid $main-color;
    border-radius: 5px;
    font-size: 14px;
    line-height: 16px;
    color: #fff;
    font-weight: 700;
    text-decoration: none;
    text-transform: uppercase;
    background-color: $main-color;

    &:hover {
      color: $main-color;
      background-color: #fff;
    }
  }
}
